<template>
  <div>
 <el-dialog
        center
        :title="dialog.title"
        :visible.sync="dialog.visible"
        width="20%"
      >
        <span>{{dialog.tipMsg}}</span>
        <span
          slot="footer"
          class="dialog-footer"
        >
          <el-button @click="dialog.visible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="dialogOkClick"
          >确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
export default {
 props: {
    dialog:{
        visible:false,
        title:'提示',
        tipMsg:'',
        trsfParm:{}
    }
  },
  methods:{
      dialogOkClick()
      {
          this.dialog.visible = false;
          this.$emit('okClick',this.dialog.trsfParm);
      }
  }
}
</script>

