<template>

  <div>
    <el-row
      type="flex"
      class="row-bg"
      justify="space-around"
    >
      <el-col
        :span="8"
        class="colGroup"
      >

        <h4 style="margin-left: 20px;">播放区域</h4>

        <div style="margin-right: 20px; margin-left: 20px;">
          <el-form
            ref="devZone"
            :inline="true"
            :rules="rule_zone"
           :model="devZone"
          >

            <el-form-item
              label="区域"
              prop="name"
            >
              <el-input
                v-model="devZone.name"
                placeholder="区域名称"
              ></el-input>
            </el-form-item>
          </el-form>

          <el-button
            type="primary"
            icon="el-icon-plus"
            @click="handleCreate('devZone')"
          >
            添加
          </el-button>

          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled='zoneDeleDisable'
            @click="handleDelete()"
          >
            删除
          </el-button>

          <div style="margin-top:20px">
            <el-table
              highlight-current-row
              :cell-class-name="tableCellClassName"
              @cell-click="handleCurrentChange"
              border
              :data="tableData"
              style="width: 100%"
              :loading="loading"
              max-height="500px"
            >

              <el-table-column
                label="名称"
                prop="name"
              >

              </el-table-column>
            </el-table>

          </div>

        </div>
      </el-col>

      <el-col
        :span="14"
        class="colGroup"
      >

        <h4 style="margin-left: 20px;">设备类型</h4>

        <div style="margin-right: 20px; margin-left: 20px;">
          <el-form
            ref="deviceType"
            :inline="true"
            :rules="rules"
            :model="deviceType"
            label-width="132px"
          >

            <el-form-item
              label="类型ID"
              prop="id"
            >
              <el-input v-model.number="deviceType.id"></el-input>
            </el-form-item>
            <el-form-item
              label="类型名称"
              prop="name"
            >
              <el-input v-model="deviceType.name"></el-input>
            </el-form-item>
            <el-form-item
              label="提前时间(秒)"
              prop="preOpenTime"
            >
              <el-input
                v-model.number="deviceType.preOpenTime"
                placeholder="单位:秒"
              ></el-input>
            </el-form-item>

            <el-form-item
              label="HD空间报警阈值"
              prop="hd"
            >
              <el-input
                v-model.number="deviceType.hd"
                placeholder="单位:GB"
              ></el-input>
            </el-form-item>
            <el-form-item label="自动开机">
              <el-checkbox v-model="deviceType.bAutoOpen"></el-checkbox>
            </el-form-item>
          </el-form>

          <el-button
            type="primary"
            icon="el-icon-plus"
            @click="addDevType('deviceType')"
          >
            添加
          </el-button>

          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled='tyoeDeleDisable'
            @click="deleteDevType"
          >
            删除
          </el-button>

          <div style="margin-top:20px">
            <el-table
              highlight-current-row
              :cell-class-name="tableCellClassName"
              @cell-click="handleCurrentChange_devType"
              border
              :data="tableData_type"
              style="width: 100%"
              :loading="loading"
              max-height="500px"
            >

              <el-table-column
                label="类型ID"
                prop="id"
              >
              </el-table-column>
              <el-table-column
                label="类型名称"
                prop="name"
              >
              </el-table-column>
              <el-table-column
                label="是否自动开机"
                prop="bAutoOpen"
                :formatter="formatBoolean"
              >
              </el-table-column>
              <el-table-column
                label="开机提前时间"
                prop="preOpenTime"
                :formatter="formatTime"
              >
              </el-table-column>
              <el-table-column
                label="硬盘空间报警阈值"
                prop="hd"
              >
              </el-table-column>
            </el-table>

          </div>

        </div>
      </el-col>
    </el-row>

    <tip-dialog
      @okClick='dialogClick'
      :dialog='dialogOb'
    ></tip-dialog>

  </div>

</template>

<script>
import tipDialog from "../tipDialog.vue";

import { Message } from "element-ui";
export default {
  components: {
    tipDialog,
  },
  data() {
    return {
      tableData: [],
      formObj: {
        name: "",
      },
      dialogOb: { visible: false, title: "提示", tipMsg: "", trsfParm: {} },
      currentRow: null,
      loading: false,
    
      devZone:{
         name:""
      },
      zoneDeleDisable: true,
      currentRow_devType: null,
      tyoeDeleDisable: true,
      deviceType: {
        id: null,
        bAutoOpen: false,
        preOpenTime: null,
        name: "",
        hd: null,
      },
      tableData_type: [],
      rules: {
        id: [
          { required: true, message: "请输入ID", trigger: "blur" },
          { type: "number", min: 0, max: 65535, message: "必须为数字值" },
        ],
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
        preOpenTime: [
          { required: true, message: "请配置时间", trigger: "blur" },
          { type: "number", min: 0, max: 3600, message: "大小要在0到3600" },
        ],
        hd: [
          { required: true, message: "请配置阈值", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
      },
      rule_zone: {
        name: [{ required: true, message: "请输入名称", trigger: "blur" }]
      },
    };
  },
  watch: {
    currentRow(newV) {
      console.log("选择", newV);
      if (newV != undefined) {
        this.zoneDeleDisable = false;
      } else {
        this.zoneDeleDisable = true;
      }
    },
    currentRow_devType(newV) {
      if (newV != undefined) {
        this.tyoeDeleDisable = false;
      } else {
        this.tyoeDeleDisable = true;
      }
    },
  },
  methods: {
    dialogClick(lparm) {
      if (lparm.isZone) {
        console.log("删除", this.currentRow);
        var bodyData = this.tableData[this.currentRow];

        const that = this;
        this.$httpApi.zoneList_delete(bodyData).then(function (response) {
          if (response.data.code == "0") {
            that.tableData.splice(that.currentRow, 1);
          } else {
            Message.error(response.data.message);
          }
        });
      } else {
        console.log("删除", this.currentRow_devType);
        bodyData = this.tableData_type[this.currentRow_devType];

        const that = this;
        this.$httpApi.typdeList_delete(bodyData).then(function (response) {
          if (response.data.code == "0") {
            that.tableData_type.splice(that.currentRow_devType, 1);
          } else {
            Message.error(response.data.message);
          }
        });
      }
    },
    handleCreate(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var bodyData = { name: this.devZone.name };
          const that = this;
          this.$httpApi.zoneList_add(bodyData).then(function (response) {
            if (response.data.code == "0") {
              that.tableData.push({ name: that.devZone.name });
            } else {
              Message.error(response.data.message);
            }
          });
        } else {
          console.log("erroe submit");
          return false;
        }
      });
    },
    handleDelete() {
      this.dialogOb.tipMsg =
        "是否删除 " + this.tableData[this.currentRow].name + " ？";
      this.dialogOb.visible = true;
      this.dialogOb.trsfParm.isZone = true;
    },
    tableCellClassName({ row, column, rowIndex, columnIndex }) {
      //注意这里是解构
      //利用单元格的 className 的回调方法，给行列索引赋值
      row.index = rowIndex;
      column.index = columnIndex;
    },
    handleCurrentChange(row) {
      //console.log(row.index);
      this.currentRow = row.index;
    },
    handleCurrentChange_devType(row) {
      this.currentRow_devType = row.index;
    },
    addDevType(formNam) {
      this.$refs[formNam].validate((valid) => {
        if (valid) {
          var bodyData = this.deviceType;
          const that = this;
          this.$httpApi.typdeList_add(bodyData).then(function (response) {
            if (response.data.code == "0") {
              that.tableData_type.push(JSON.parse(JSON.stringify(bodyData)));
            } else {
              Message.error(response.data.message);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    deleteDevType() {
      this.dialogOb.tipMsg =
        "是否删除 " + this.tableData_type[this.currentRow_devType].name + " ？";
      this.dialogOb.visible = true;
      this.dialogOb.trsfParm.isZone = false;
    },
    formatBoolean: function (row, column, cellValue) {
      var ret = ""; //你想在页面展示的值
      if (cellValue) {
        ret = "是"; //根据自己的需求设定
      } else {
        ret = "否";
      }
      return ret;
    },

    formatTime: function (row, column, cellValue) {
      const h = parseInt(cellValue / 3600);
      const minute = parseInt((cellValue / 60) % 60);
      const second = Math.ceil(cellValue % 60);

      const hours = h < 10 ? "0" + h : h;
      const formatSecond = second > 59 ? 59 : second;
      return `${hours > 0 ? `${hours}:` : ""}${
        minute < 10 ? "0" + minute : minute
      }:${formatSecond < 10 ? "0" + formatSecond : formatSecond}`;
    },
  },

  mounted() {
    const that = this;
    that.loading = true;
    this.$httpApi
      .zoneList_get()
      .then(function (response) {
        that.loading = false;
        //console.log(response.data);
        if (response.data.code == "0") {
          //console.log(response.data.zoneList);
          that.tableData = response.data.zoneList;
          //Vue.set(that.tableData, response.data.zoneList);
          //console.log(that.tableData);
        }
      })
      .catch(function () {
        that.loading = false;
      });

    this.$httpApi
      .typdeList()
      .then(function (response) {
        // console.log(response.data);
        if (response.data.code == "0") {
          // console.log(response.data.dataList);
          that.tableData_type = response.data.dataList;
          //Vue.set(that.tableData, response.data.zoneList);
          //console.log(that.tableData_type);
        }
      })
      .catch(function () {});
  },
};
</script>

<style>
.colGroup {
  border: 1px solid lightgray;
  height: 100vh;
  margin-bottom: 20px;
}
.demo-input-suffix {
  display: flex;
  justify-content: baseline;
}
.tipTile {
  width: 100px;
}
</style>